.chapter-content {
    margin-top: 20px;
}

.chapter-content h2,
.chapter-content h3 {
    margin-top: 1.5em;
    margin-bottom: 0.5em;
}

.chapter-content pre {
    background: #f5f5f5;
    padding: 15px;
    border-radius: 4px;
    overflow-x: auto;
}

.loading, .error {
    text-align: center;
    padding: 40px;
    font-size: 1.2em;
}

.error {
    color: #e74c3c;
}